let globals = {
  mockEnv: 'test',
  version: '1.0', // 版本号
  unifiedLogin: '',
  host: 'http://192.168.98.190:8071/', // 接口地址
  domain: '',
  myStar: 'ik_saas%&(+||',
  youStar: 'iksaas#$%^&*(',
  isIE: navigator.userAgent.indexOf('compatible') > -1 && navigator.userAgent.indexOf('MSIE') > -1,
  isIE11: navigator.userAgent.indexOf('Trident') > -1 && navigator.userAgent.indexOf('rv:11.0') > -1,
  isIE9: navigator.userAgent.indexOf('MSIE 9.0') !== -1,
  hostServe: {
    test: 'http://localhost:9000/',
    // test: 'http://192.168.98.190:8073/api/',
    // test: 'http://192.168.98.184:8072/api/',
    // test: '//test.saas.ik.com/api/',
    // test: 'http://192.168.99.43:8071/api/',
    // test: 'http://172.16.98.115:8885/api/',
    // test: 'http://172.16.98.230:8080/api/',
    // test: 'http://192.168.99.125:8885/api/',
    // test: '//192.168.98.190:8071/',
    // test: 'http://192.168.98.186:8885/',
    uat: '//uat.saas.ik.com/api/',
    pre: '//saas.ik.com/api/',
    prod: '//saas.ik.com/api/'
  },
  domainServe: {
    test: '.ik.com',
    uat: '.ik.com',
    pre: '.ik.com',
    prod: '.ik.com'
  },
  cookieProp: { path: '/' },
  Reg: {
    verifyPass: /\w{6,20}/,
    verifyAddUserPass: /^[a-zA-Z0-9]{6,20}$/,
    verifyLoginName: /\w{4,20}/,
    verifyMSM: /^\d{6}$/,
    verifyNOChinese: /[^\u4e00-\u9fa5]+/,
    verifyMobile: /^(13[0-9]|14[579]|15[0-3,5-9]|16[67]|17[0135678]|18[0-9]|19[189])\d{8}$/,
    verifyPhone: /(^(\d{3}-)?\d{8})$|(1[0-9]{10})/,
    verifyIdCard: /^(\d{18,18}|\d{15,15}|\d{17,17}[x,X])$/,
    verifyRoom: /^[0-9a-zA-Z]$/, // 房间号
    verifyByName: /[a-zA-Z\d\u4e00-\u9fa5]/ // 别名过滤特殊字符
  },
  init () {
    let nowHostValue = window.location.hostname
    let preIndex = nowHostValue.indexOf('.')
    let preHostName = nowHostValue.substring(0, preIndex)
    if (nowHostValue.indexOf('192.168.98.190') > -1) {
      globals.host = 'http://192.168.98.190:8885/'
      globals.domain = ''
    } else if (nowHostValue === 'localhost' || nowHostValue.indexOf('192.168.') > -1 || nowHostValue.indexOf('172.16.') > -1 || nowHostValue.indexOf('127.0.') > -1) {
      let mockEnv = globals.mockEnv
      globals.host = globals.hostServe[mockEnv]
      globals.domain = ''
    } else if (preHostName === 'saas' || preHostName === 'pre') {
      globals.host = globals.hostServe['prod']
      globals.domain = globals.domainServe['prod']
    } else {
      globals.host = globals.hostServe[preHostName]
      globals.domain = globals.domainServe[preHostName]
    }
  }
}
export default globals



